:root {
  --primary-font: DM Sans, sans-serif;
  --primary-text-black: #000;
  --grey-text-color: #907e82;
  --white-text-color: #fff;
  --main-bgc: #fff;
  --secondary-bgc: #fff5f6;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  color: var(--primary-text-color);
  font-family: var(--primary-font), sans-serif;
  letter-spacing: .04em;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.79;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
  display: block;
}

.upper-title {
  text-align: center;
  text-transform: uppercase;
  color: #ffa5ba;
  font-family: Titan One;
  font-size: 18px;
  line-height: 1.17;
}

@media screen and (min-width: 768px) {
  .upper-title {
    font-size: 26px;
    line-height: 1.15;
  }
}

@media screen and (min-width: 1280px) {
  .upper-title {
    font-size: 30px;
    line-height: 1.13;
  }
}

.title {
  text-align: center;
  text-transform: uppercase;
  color: #d41443;
  font-family: Titan One;
  font-size: 34px;
  line-height: 1.15;
}

@media screen and (min-width: 768px) {
  .title {
    font-size: 48px;
  }
}

@media screen and (min-width: 1280px) {
  .title {
    font-size: 58px;
    line-height: 1.14;
  }
}

.btn {
  min-width: 107px;
  height: 44px;
  cursor: pointer;
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 21px;
  display: inline-flex;
}

.btn--red {
  color: #fff;
  background-color: #d41442;
}

.btn--white {
  color: #d41443;
  background-color: #fff;
}

.btn--hero {
  height: 40px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29px;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
}

.list, .link {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.products__upper-title {
  margin-bottom: 15px;
}

.products__title {
  margin-bottom: 140px;
}

.products__item:not(:last-child) {
  margin-bottom: 143px;
}

.card-contents {
  text-align: center;
  width: 280px;
  height: 489px;
  background-color: #ffb8ca;
  border-radius: 24px;
  padding-left: 40px;
  padding-right: 40px;
  position: relative;
}

.card-contents--yellow {
  background-color: #f0d1a5;
}

.card-contents--green {
  background-color: #c2e297;
}

.products__image {
  z-index: 10;
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
  top: -28%;
  left: 0;
}

.products__icon {
  width: 15px;
  height: 15px;
  fill: #d41443;
}

.card-contents__icon {
  margin-bottom: 32px;
}

.card-contents__subtitle {
  text-align: center;
  letter-spacing: 1.15px;
  text-transform: uppercase;
  color: var(--white-text-color);
  margin-bottom: 32px;
  padding-top: 160px;
  font-family: Titan One;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 23px;
}

.card-contents__text {
  color: var(--white-text-color);
  width: 200px;
  letter-spacing: .04em;
  margin-bottom: 32px;
  font-weight: 700;
  line-height: 1.5;
}

.card-contents__text:before {
  content: "";
  height: 6px;
  background-image: url("group.8e4d68eb.svg");
  background-position: center;
  background-repeat: no-repeat;
  margin-bottom: 32px;
  display: block;
}

.products__btn {
  background-color: var(--white-text-color);
  width: 40px;
  height: 40px;
  border: rgba(0, 0, 0, 0);
